import React from 'react';
import Container from 'react-bootstrap/Container'
import Button from 'react-bootstrap/Button';
import Navbar from 'react-bootstrap/Navbar';
import Nav from 'react-bootstrap/Nav';
import NavDropdown from 'react-bootstrap/NavDropdown';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col'
import Form from 'react-bootstrap/Form';
import FormControl from 'react-bootstrap/FormControl';
import Image from 'react-bootstrap/Image';
import Title from '../Assets/title.png';

function Top() {
    return (
      <div className="Nav">
          <React.Fragment>
                    
                <Container>
                <Row>
                    <Col xs = "12" lg = "12" style = {{textAlign : "center", marginTop : "15px"}}>
                    <Image src = {Title}/>
                    </Col>
                </Row>

                
                    <Navbar bg = "light" expand = "lg">
                    <Navbar.Brand  href="#home"> </Navbar.Brand>
                    <Navbar.Toggle aria-controls="basic-navbar-nav" />
                        <Navbar.Collapse id="basic-navbar-nav">
                        <Nav className = "mr-auto"  Align = "right">
                            <Nav.Link style = {{backgroundColor : 'coral'}} href="#home">Home</Nav.Link>
                            <Nav.Link href= "/home" >Undangan Online</Nav.Link>
                            <Nav.Link href= "/products">Our Products</Nav.Link>
                            <Nav.Link href= "/cart">Cart</Nav.Link>
                            <Nav.Link href= "/aboutus">About Us</Nav.Link>
                            <Nav.Link href= "/contactus">Contact Us</Nav.Link>
                        </Nav>
                    
                            <NavDropdown title="Categories" Align = "center">
                            <NavDropdown.Item href="#action/3.1" Align = "right">Vintage</NavDropdown.Item>
                            <NavDropdown.Divider />
                            <NavDropdown.Item href="#action/3.2">Cute</NavDropdown.Item>
                            <NavDropdown.Divider />
                            <NavDropdown.Item href="#action/3.3">Elegant</NavDropdown.Item>
                            <NavDropdown.Divider />
                            <NavDropdown.Item href="#action/3.4">Youthful</NavDropdown.Item>
                            </NavDropdown>
                        <Form inline >
                            
                            <FormControl type="text" placeholder="Your Invitation" className="mr-sm-2" />
                            <Button>Search</Button>
                        </Form>
                        </Navbar.Collapse>
                    </Navbar>  
                </Container>
                
          </React.Fragment>
      </div>
    );
  }
  
  export default Top;
  